import authAPi from "@/api/auth.js";
import { setItem } from "@/helpers/persistanceStorage";

import router from "@/router";

const state = {
  isSubmitting: false,
  isLoading: false, // 
  isAuthenticated: false, //  
  validationErrors: null, //
};

export const mutationTypes = {
  loginStart: "[auth] loginStart",
  loginSuccess: "[auth] loginSuccess",
  loginFailure: "[auth] loginFailure",

  exitLoginSuccess: "[auth] exit success",
};
export const actionTypes = {
  login: "[auth] login",
  exitLogin: "[auth] exit",
};

const mutations = {
  [mutationTypes.loginStart](state) {
    state.isSubmitting = true;
    state.isAuthenticated = false;
    state.validationErrors = null;
  },
  [mutationTypes.loginSuccess](state, payload) {
    state.isSubmitting = false;
    state.isAuthenticated = true;

    state.isLoggedIn = true;
  },
  [mutationTypes.loginFailure](state, payload) {
    state.isSubmitting = false;
    state.isAuthenticated = false;
    state.validationErrors = payload;
    console.log(state.validationErrors);
  },
  [mutationTypes.exitLoginSuccess](state) {
    state.isSubmitting = false;
    state.isAuthenticated = true;
  },
};
const actions = {
  [actionTypes.exitLogin](context) {
    context.commit(mutationTypes.exitLoginSuccess);
    window.localStorage.setItem("accessTokenStore", "");
    setItem("accessTokenStore", ""); //     
    window.localStorage.setItem("currentUser", []);
    setItem("currentUser", []); //  
    window.localStorage.setItem("currentWepon", []);
    setItem("currentWepon", []); //  
    router.push({ name: "login" });
  },
  [actionTypes.login](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.loginStart);
      authAPi
        .login(credentials)
        .then((response) => {
          context.commit(mutationTypes.loginSuccess, response.data);
          window.localStorage.setItem("accessTokenStore", response.data.token);
          setItem("accessTokenStore", response.data.token); //     
          resolve(response.data);
        })
        .catch((resp) => {
          context.commit(mutationTypes.loginFailure, resp.data);
          console.log("", resp);
        });
    });
  },
};
export default {
  state,
  mutations,
  actions,
};
