<template>
  <div class="container-login">
    <div class="login">
      <div class="login__img">
        <img src="../assets/img/logo-auth.png" alt="Logo" />
      </div>
      <!-- <AppValidationErrors
        v-if="validationErrors"
        :validation-errors="validationErrors"
      /> -->
      <form @submit.prevent="onSubmit" class="form">
        <fieldset class="form-group">
          <input
            type="text"
            class="form-control"
            placeholder=""
            name="email"
            v-model="email"
          />
        </fieldset>
        <fieldset class="form-group">
          <input
            type="password"
            class="form-control"
            name="password"
            placeholder=""
            v-model="password"
          />
        </fieldset>
        <input
          type="button"
