<template>
  <div class="active-block">
    <p class="mini-title"> </p>
    <div class="active-block-input">
      <label for="active"> </label>
      <div class="active-block-error" v-if="this.statusActivation">
        <div
          class="active-block-error__fail"
          v-if="this.statusActivation.status === 'FAIL'"
        >
          <p class="active-block-error__error-input">
            {{ this.statusActivation.message }}
          </p>
        </div>
      </div>
      <input
        maxlength="14"
        placeholder="xxxxxxxxxxxxxx"
        v-model.trim="$v.serial.$model"
        class="seria-input serial"
      />
      <div class="loader" v-if="isLoading">
        <p class="loader-title">Loading...</p>
        <div class="wrapper">
          <div class="clock"></div>
          <div class="clock"></div>
        </div>
      </div>
      <div class="registr-block__button">
        <button
          type="button"
          class="button btn-primary"
          :disabled="isLoading === true"
          @click="active()"
        >
          
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  required,
  maxLength,
  minLength,
  email,
  numeric,
  helpers,
} from "vuelidate/lib/validators";
import { actionTypes } from "@/store/modules/register";
import { mapState } from "vuex";
import router from "@/router";
export default {
  name: "AppActiveBlock",
  data() {
    return {
      activeBlock: {},
      serial: "",
      passport_id: "",
    };
  },
  validations: {
    serial: {
      minLength: minLength(14),
      maxLength: maxLength(14),
      required,
    },
  },

  computed: {
    ...mapState({
      currentUser: (state) => state.register.currentUser,
      getWeaponItem: (state) => state.register.getWeaponItem,
      isLoading: (state) => state.register.isLoading,
      statusActivation: (state) => state.register.statusActivation,
    }),
  },
  methods: {
    active() {
      this.activeBlock = {
        passportSerial: this.currentUser.passportData.passportSerial,
        passportNumber: this.currentUser.passportData.passportNumber,
        serial: this.serial,
        weaponId: this.getWeaponItem.id,
      };
      this.$store
        .dispatch(actionTypes.getSerial, this.activeBlock)
        .then((response) => {
          if (this.statusActivation.status === "SUCCESS") {
            router.push({ name: "success" });
          }
        })
        .catch((er) => {
          console.log("", er);
        });
      this.passport_id = this.currentUser.passportData.passportSerial.concat(
        this.currentUser.passportData.passportNumber
      );
    },
    success() {},
  },
};
</script>

