<template>
  <div class="clients">
    <div class="container">
      <div class="clients-wrap">
        <div class="clients__select">
          <select id="standard-select" class="text-input" v-model="select">
            <option value="serial">   :</option>
            <option value="phone">   :</option>
            <!-- <option value="name">  :</option> -->
          </select>
        </div>
        <div class="clients__search-container">
          <div
            class="search-container2"
            :class="{ activeBlock: this.select === 'serial' }"
          >
            <div class="clients__search">
              <input
                type="search"
                id="search-bar"
                name="search"
                placeholder="   "
                v-model="search"
                aria-label=""
                class="input-search"
              />
              <!-- <button @click="onChange(search)"> -->
              <img
                src="../assets/img/search-icon.png"
                class="search-icon"
                alt="Search"
                @click="onChange(search)"
              />
            </div>
          </div>
          <div
            class="search-container2"
            :class="{ activeBlock: this.select === 'phone' }"
          >
            <div class="clients__search">
              <the-mask
                :mask="['+7(###)-###-##-##']"
                id="search-bar"
                name="phone"
                class="text-input"
                placeholder="+7(___)___-__-__"
                v-model="phone"
              />
              <!-- <input
              type="search"
              id="search-bar"
              name="phone"
              placeholder="  "
              v-model="phone"
              aria-label=""
              class="input-search"
            /> -->
              <!-- <button @click="onChange(search)"> -->
              <img
                src="../assets/img/search-icon.png"
                class="search-icon"
                alt="Search"
                @click="searchPhone(phone)"
              />
            </div>
            <!-- </button> -->
          </div>
        </div>
      </div>
      <div class="clients__row">
        <div class="clients__error" v-if="this.validationErrors">
          <div
            class="mini-title"
            v-if="this.validationErrors.status === 'FAIL'"
          >
            {{ this.validationErrors.message }}
          </div>
        </div>
        <div class="clients__column" v-if="this.allClients">
          <ItemClients v-bind:client_item="this.allClients" />
          <PutItemClient v-bind:client_item="this.allClients" />
        </div>
      </div>
      <div class="" v-if="this.allClients">
        <p class="mini-title"> </p>
        <BlocksWeapon
          v-for="weapon in this.allClients.unitInfo"
          :key="weapon.id"
          v-bind:weapon_item="weapon"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { TheMask } from "vue-the-mask";
import ItemClients from "@/components/ItemClients.vue";
import PutItemClient from "@/components/PutItemClient.vue";
import BlocksWeapon from "@/components/BlocksWeapon.vue";
import { actionTypes } from "@/store/modules/clients";
import { mapState } from "vuex";
export default {
  name: "AllClients",
  data() {
    return {
      search: "",
      phone: "",
      select: "phone",
    };
  },
  components: {
    ItemClients,
    TheMask,
    PutItemClient,
    BlocksWeapon,
  },
  computed: {
    ...mapState({
      allClients: (state) => state.clients.allClients,
      validationErrors: (state) => state.clients.validationErrors,
    }),
  },
  mounted() {
    // this.$store.dispatch(actionTypes.getAllclients);
  },
  methods: {
    onChange() {
      this.$store
        .dispatch(actionTypes.getSearchClient, this.search)
        .then(() => {})
        .catch((error) => {
          console.log(error);
        });
    },
    searchPhone() {
      this.$store
        .dispatch(actionTypes.getSearchPhoneClient, this.phone)
        .then(() => {})
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

