import axios from "@/api/axios";

const contact = (credentials) => {
  // console.log(credentials);
  return axios.post("/api/1/registration", credentials);
};
const PutContact = (credentials) => {
  console.log(credentials);
  return axios.post(`/api/1/change/user/${credentials.id}`, credentials);
};
export default {
  contact,
  PutContact,
};
