                required
              />
            </div>
            <div class="registr-block__wrap">
              <div class="label-error-confidant">
                <label for="relationship"> :</label>
              </div>
              <div class="select">
                <select
                  id="standard-select"
                  class="text-input"
                  name="relationship"
                  v-bind:value="this.client_item.confidantData.relationship"
                  required
                >
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value="/">/</option>
                  <option value=""></option>
