import ClientsAPi from "@/api/clients.js";
import { setItem } from "@/helpers/persistanceStorage";

const state = {
  isSubmitting: false,
  isLoading: false, // 
  validationErrors: null, //
  allClients: null,
};

export const mutationTypes = {
  allClientsStart: "[clients] allClientsStart",
  allClientsSuccess: "[clients] allClientsSuccess",
  allClientsFailure: "[clients] allClientsFailure",

  getSearchClientStart: "[clients] getSearchClientStart",
  getSearchClientSuccess: "[clients] getSearchClientSucceess",
  getSearchClientFailure: "[clients] getSearchClientFailure",

  getSearchPhoneClientStart: "[clients]  getSearchPhoneClientStart",
  getSearchPhoneClientSuccess: "[clients]  getSearchPhoneClientSucceess",
  getSearchPhoneClientFailure: "[clients]  getSearchPhoneClientFailure",

  deleteClientStart: "[clients] deleteClientStart",
  deleteClientSuccess: "[clients] deleteClientSuccess",
  deleteClientFailure: "[clients] deleteClientFailure",
};
export const actionTypes = {
  getAllclients: "[clients] clients",
  deleteClient: "[clients] deleteClient",
  getSearchClient: "[client] getSearchClient",
  getSearchPhoneClient: "[client] getSearchPhoneClient",
};

const mutations = {
  [mutationTypes.allClientsStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.allClientsSuccess](state, payload) {
    state.isLoading = false;
    state.allClients = payload.data.result;
  },
  [mutationTypes.allClientsFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
  [mutationTypes.getSearchClientStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.getSearchClientSuccess](state, payload) {
    state.isLoading = false;
    state.allClients = payload.data.result;
    state.validationErrors = payload.data;
  },
  [mutationTypes.getSearchClientFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },

  [mutationTypes.getSearchPhoneClientStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.getSearchPhoneClientSuccess](state, payload) {
    state.isLoading = false;
    state.allClients = payload.data.result;
    state.validationErrors = payload.data;
  },
  [mutationTypes.getSearchPhoneClientFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },

  [mutationTypes.deleteClientStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.deleteClientSuccess](state) {
    state.isLoading = false;
  },
  [mutationTypes.deleteClientFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
};
const actions = {
  [actionTypes.getAllclients](context) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.allClientsStart);
      ClientsAPi.AllClients()
        .then((response) => {
          context.commit(mutationTypes.allClientsSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.allClientsFailure, resp);
          console.log("", resp);
        });
    });
  },
  [actionTypes.getSearchClient](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getSearchClientStart);
      ClientsAPi.getSearchClients(credentials)
        .then((response) => {
          context.commit(mutationTypes.getSearchClientSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.getSearchClientFailure, resp);
          console.log("", resp);
        });
    });
  },
  [actionTypes.getSearchPhoneClient](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getSearchPhoneClientStart);
      ClientsAPi.getSearchPhoneClient(credentials)
        .then((response) => {
          context.commit(mutationTypes.getSearchPhoneClientSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.getSearchPhoneClientFailure, resp);
          console.log("", resp);
        });
    });
  },
  [actionTypes.deleteClient](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.deleteClientStart);
      ClientsAPi.deleteClient(credentials)
        .then((response) => {
          context.commit(mutationTypes.deleteClientSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.deleteClientFailure, resp);
          console.log("", resp);
        });
    });
  },
};
export default {
  state,
  mutations,
  actions,
};
