      confidantMiddleName: "",
      confidantPhone: "",
      relationship: "",

      isActiveBlock: false,
    };
  },
  validations: {
    lastName: {
      required,
      alpha: helpers.regex("alpha", /^[--]*$/),
    },

    firstName: {
      required,
      alpha: helpers.regex("alpha", /^[--]*$/),
    },
    middleName: {
      required,
      alpha: helpers.regex("alpha", /^[--]*$/),
    },
    email: {
      required,
      email,
    },
    phone: {
      minLength: minLength(10),
      required,
      //  pattern="\+7\s?[\(]{0,1}9[0-9]{2}[\)]{0,1}\s?\d{3}[-]{0,1}\d{2}[-]{0,1}\d{2}"
    },
    passportSerial: {
      required,
      numeric,
    },
    passportNumber: {
      required,
      numeric,
    },
    registrationAddress: {
      required,
    },
    passportPlaceOfIssue: {
      required,
      alpha: helpers.regex("alpha", /^[--\s]+$/),

      minLength: minLength(2),
      maxLength: maxLength(150),
    },
    passportNumber: {
      minLength: minLength(6),
    },
    passportSerial: {
      minLength: minLength(4),
    },
    passportCode: {
      required,
      numeric,
    },
    birthDate: {
      required,
      alpha: helpers.regex("alpha", /^[0-9]{4}\-[0-9]{2}\-[0-9]{2}$/),
    },
    passportDateOfIssue: {
      required,
      alpha: helpers.regex("alpha", /^[0-9]{4}\-[0-9]{2}\-[0-9]{2}$/),
    },
    confidantFirstName: {
      required,
      alpha: helpers.regex("alpha", /^[--]*$/),
    },
    confidantLastName: {
      required,
      alpha: helpers.regex("alpha", /^[--]*$/),
    },
    confidantMiddleName: {
