  },
  methods: {
    submit() {
      if (
        this.confidantFirstName.length ||
        this.confidantLastName.length ||
        this.confidantMiddleName.length ||
        this.confidantPhone.length ||
        this.relationship.length
      ) {
        this.$v.$touch();
        if (this.$v.$invalid) {
          this.submitStatus = "ERROR in first";
        } else {
          this.request();
        }
      } else {
        this.$v.lastName.$touch();
        this.$v.firstName.$touch();
        this.$v.middleName.$touch();
        this.$v.email.$touch();
        this.$v.phone.$touch();
        // 
        this.$v.passportSerial.$touch();
        this.$v.passportNumber.$touch();
        this.$v.registrationAddress.$touch();
        this.$v.passportPlaceOfIssue.$touch();
        this.$v.passportCode.$touch();
        this.$v.birthDate.$touch();
        this.$v.passportDateOfIssue.$touch();
        if (
          this.$v.lastName.$invalid ||
          this.$v.firstName.$invalid ||
          this.$v.middleName.$invalid ||
          this.$v.email.$invalid ||
          this.$v.phone.$invalid ||
          this.$v.passportSerial.$invalid ||
          this.$v.passportNumber.$invalid ||
          this.$v.passportPlaceOfIssue.$invalid ||
          this.$v.passportCode.$invalid ||
          this.$v.birthDate.$invalid ||
          this.$v.passportDateOfIssue.$invalid
        ) {
          this.submitStatus = "ERROR second";
        } else {
          this.request();
        }
      }
    },
    ChangeConfidant(value) {
      console.log(value);
      this.confidantData = value;
    },
    request() {
      this.$store
        .dispatch(actionTypes.contact, this.item)
        .then(() => {
          if (this.contact.data.status === "SUCCESS") {
            this.$store
              .dispatch(actionTypes.getDataContact, this.item)
              .then(() => {
                this.$emit("validFirstBlock");
              })
              .catch((er) => {
                console.log(er, "Error");
              });
          }
        })
        .catch((er) => {
          console.log("", er);
        });
    },
    changeFirstBlock() {
      this.$emit("validFirstBlock");
    },

    validFirstBlock() {
      //  
      this.passportData = {
        registrationAddress: this.registrationAddress,
        passportPlaceOfIssue: this.passportPlaceOfIssue,
        departmentCode: this.passportCode,
        passportDateOfIssue: this.passportDateOfIssue,
        passportSerial: this.passportSerial,
        passportNumber: this.passportNumber,
        birthDate: this.birthDate,
      };
      //  
      if (
        this.confidantFirstName.length &&
        this.confidantLastName.length &&
        this.confidantMiddleName.length &&
        this.confidantPhone.length &&
        this.relationship.length
      ) {
        this.confidantData = {
          confidantFirstName: this.confidantFirstName,
          confidantLastName: this.confidantLastName,
          confidantMiddleName: this.confidantMiddleName,
          confidantPhone: this.confidantPhone,
          relationship: this.relationship,
        };
      } else this.confidantData = null;

      //  
      this.item = {
        lastName: this.lastName,
        firstName: this.firstName,
        middleName: this.middleName,
        email: this.email,
        contactPhone: this.phone,
        passportData: this.passportData,
        confidantData: this.confidantData,
      };
      // this.$emit("validFirstBlock");
      this.submit();
    },
